.plans {
}
.plans__inner {
  padding-bottom: 5.5rem;
}
.plans__title {
  margin-bottom: 6rem;
}
.chunk-title {
}
.plans__content {
}
.plans__item {
  margin-top: 5rem;
}
.plans__item-title {
  padding: 3rem 0 3rem 4rem;
  background: #014493;
  border-radius: 1rem;
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 2.8rem;
  text-transform: uppercase;
  color: #FFFFFF;
}

.plans__econom-head,
.plans__middle-head,
.plans__business-head,
.plans__cross-head,
.plans__minivan-head {
  font-size: 2rem;
  line-height: 2.5rem;
  color: #4F89CD;
}
.plans-row {
  display: grid;
  grid-template-columns: minmax(14rem, 32rem) minmax(9rem, 20rem) repeat(6, minmax(5rem, 12rem));
  padding: 2rem 1rem 2rem 4rem;
  min-width: 65rem;
}
.plans__econom-item,
.plans__middle-item,
.plans__business-item,
.plans__cross-item,
.plans__minivan-item {
  background-color: rgba(1, 68, 147, 0.05);
  font-weight: 400;
  color: #333333;
  border-bottom: 1px solid #014493;
  font-size: 1.8rem;
  line-height: 2.3rem;
}
.plans__business-item {
  border-radius: 1rem;
}

.plans__econom-item:first-child,
.plans__middle-item:first-child,
/* .plans__business-item:first-child, */
.plans__cross-item:first-child,
.plans__minivan-item:first-child {
  border-radius: 1rem 1rem 0 0;
}
.plans__econom-item:last-child,
.plans__middle-item:last-child,
/* .plans__business-item:last-child, */
.plans__cross-item:last-child,
.plans__minivan-item:last-child {
  border-radius: 0 0 1rem 1rem;
}
.plans__econom-item:last-child,
.plans__middle-item:last-child,
.plans__business-item:last-child,
.plans__cross-item:last-child,
.plans__minivan-item:last-child {
  border-bottom: none;
}
/* .plans__econom-wrapper {
  width: 100%;
} */
.plans__car-name {
  font-weight: 700;
}