.footer {
  background-color: #014493;
  width: 100%;
}
.footer a {
  color: #ffffff;
}
.footer__inner {
  font-family: $secondFont;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.footer__logo {
  padding: 4.2rem 0 4.8rem 0;
}
.footer__logo img {
  max-width: 18.8rem;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  flex: 1 1 100%;
  align-items: center;
  margin-left: 9.2rem;
}
.footer__menu {
  position: relative;
}
.footer__menu-list {
  //padding: 4.4rem 0;
  display: flex;
  gap: 5rem;
}
.footer__menu-item {
}
.footer__menu-link {
}
.footer__social {
  display: flex;
  position: relative;
  line-height: 3.2rem;
}
.footer__social-list {
  display: flex;
  gap: 1.6rem;
}
.footer__social-link {
  width: 3.6rem;
  height: 3.6rem;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.footer__social-item {
}
.footer__email-link {
  margin-right: 4rem;
}
.footer__phone-link {
  position: absolute;
  bottom: -2rem;
  left: 0;
}
.footer .footer__menu-link--bottom {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #4F89CD;
  position: absolute;
  top: 4.2rem;
}