.header {
  width: 100%;
  background-color: #014493;
}
.header__inner {
  font-family: $secondFont;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.header__logo {
  padding: 1.8rem 0 1.2rem 0;
}
.header__logo img {
  max-width: 18.8rem;
}
.header__content {
  display: flex;
  justify-content: space-between;
  flex: 1 1 100%;
  align-items: center;
  margin-left: 9.2rem;
}
.header__menu {
}
.header__menu-list {
  //padding: 4.4rem 0;
  display: flex;
  gap: 5rem;
}
.header__menu-item {
}
.header__menu-link {
  color: #ffffff;
}
.header__social {
  display: flex;
  align-items: center
}
.header__social-list {
  display: flex;
  gap: 1.6rem;
}
.header__email-link {
  margin-right: 4rem;
  color: #fff;
}
.header__social-item {

}
.header__social-link {
  width: 3.6rem;
  height: 3.6rem;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}