.terms {
}
.terms .container {
  padding: 5.5rem 0;
}
.terms__inner {
  padding: 4rem 6rem 5rem 6rem;
  background: rgba(1, 68, 147, 0.1);
  border-radius: 1rem;
}
.terms__title {
  margin-bottom: 3.9rem;
}
.terms__content {
  display: flex;
  justify-content: space-between;
}
.terms__item {
}
.terms__caption {
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 2.8rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.terms__item-list {
}
.terms__list-item {
  margin-left: 2rem;
  margin-bottom: 2rem;
  position: relative;
}
.terms__list-item::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -1.6rem;
  width: 0.8rem;
  height: 0.8rem;
  background-color: #014493;
  border-radius: 0.1rem;
  transform: translate(-50%, -50%);
}
.terms__item:last-child .terms__list-item::before {
  background-color: #FF2219;
}
.terms__item:nth-child(2) .terms__list-item:nth-child(3)::before {
  top: 25%;
}
.terms__list-item:last-child {
  margin-bottom: 0;
}